.slider {
  position: relative;
  width: 266px;
  height: 6px;
  margin-top: 20px;

  background: #FFFFFF;

  border: 1px solid rgba(31, 32, 65, 0.25);
  box-sizing: border-box;
  border-radius: 3px;
}
.slider-ball-1 {
  position: absolute;
  width: 12px;
  height: 12px;

  margin-top: -6px;
  margin-left: 70px;


  cursor: pointer;
  z-index: 1;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
  border: 2px solid #FFFFFF;
  border-radius: 10px;
}
.slider-betweenball{
  position: absolute;
  width: 100px;
  height: 4px;
  border-radius: 3px;

  margin-left: 75px;

  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
}
.slider-ball-2 {
  position: absolute;
  width: 12px;
  height: 12px;

  margin-top: -6px;
  margin-left: 170px;

  
  cursor: pointer;
  z-index: 1;
  background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
  border: 2px solid #FFFFFF;
  border-radius: 10px;
}